* {
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
}
img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 600;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgb(255, 0, 64) 0%,
    rgb(196, 0, 196) 50%,
    rgb(255, 0, 64) 100%
  );
}
section {
  min-width: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 100px 0;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgb(255, 0, 64) 0%,
    rgb(196, 0, 196) 50%,
    rgb(255, 0, 64) 100%
  );
}
.container {
  width: 100%;
  height: auto;
  max-width: 100%;
  margin: 0 auto;
  float: right;
}
/* a.view-btn {
  float: right;
  margin-top: -30px;
} */
.brand {
  text-align: center;
  font-size: 4rem;
  /* color: #000; */
  color: #091025;
  text-shadow: 1px 1px 1px #6feb3ff1;
  transition: text-shadow 300ms ease-out;
  margin-bottom: 30px;
}
.brand:hover {
  color: #000;
  text-shadow: 0 0 1px #fff, 0 0 2px #fff, 0 0 5px #fff, 0 0 8px #48cae4,
    0 0 10px #48cae4, 0 0 14px #00b4d8, 0 0 20px #00b4d8, 0 0 30px #0096c7,
    0 0 40px #00b5f1, 0 0 60px #01baf8;
}
form {
  width: 95%;
  max-width: 500px;
  height: 50px;
  margin: 20px auto;
  background-color: rgb(223, 223, 223);
  border-radius: 8px;
}
form input {
  width: 90%;
  background-color: transparent;
  padding: 10px;
  border-radius: 4px;
  border: none;
  outline: none;
  font-size: 2rem;
  display: inline-block;
}
form ion-icon {
  width: 9%;
  font-size: 2.5rem;
  margin: -9px;
  color: rgb(32, 28, 28);
}
.search-result {
  margin-top: 50px;
  width: 100%;
  height: max-content;
  display: grid;
  grid-gap: 25px;
  grid-template-columns: repeat(4, 1fr);
  /* opacity: 0;
  transition: .5s ease; */
}
/* .container:hover .search-result{
  opacity: 1;
} */
button {
  height: 49px;
  width: 90px;
  margin-left: 84%;
  font-weight: bold;
  float: left;
  margin-top: -47px;
  border: none;
  font-size: 18px;
  border-radius: 8px;
  box-shadow: -0.5px 0.5px 3px black;
  color: #000;
  cursor: pointer;
  /* background-color: rgb(105, 105, 238); */
}
button:hover {
  color: #fff;
  cursor: pointer;
  background-color: rgb(105, 105, 238);
}
.itm {
  width: 100%;
  margin-left: 15px;
  border-radius: 8px;
  background-color: rgb(37, 37, 37);
  padding: 15px;
  overflow: hidden;
}
.itm img {
  width: 100%;
  height: 200px;
  border-radius: 4px;
  cursor: pointer;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}
.itm img:hover {
  background-color: #3e8e41;
  color: red;
  opacity: 0.3;
  transform: scale(1.1);
}
/* .itm img:hover{

} */
.itm .flex-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.itm .title {
  color: whitesmoke;
  margin: 20px 10px 0 0;
  /* font-weight: 400; */
}
.view-btn {
  text-decoration: none;
  text-align: center;
  width: 130px;
  padding: 10px 0;
  background-color: #404040;
  color: white;
  border-radius: 5pc;
  font-size: 1.4rem;
  margin-top: 20px;
  align-self: flex-start;
}
.view-btn:hover {
  background-color: rgb(158, 154, 154);
  color: #000;
}
.itm-data {
  color: rgb(0, 0, 0);
  display: block;
  margin: 15px;
  padding: 5px;
  margin-top: 10px;
  font-size: 3rem;
  /* font-weight: bold; */
  /* letter-spacing: 0.1rem; */
  line-height: 4rem;
  font-style: normal;
  border: 1px solid rgb(10, 10, 10);
  text-align: center;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgb(141, 136, 228) 0%,
    rgb(140, 218, 231) 30%,
    rgb(146, 223, 238) 100%
  );
}
